import { types } from "../types";

const initialState = {
    empty: true,
    friends: []
};

export const friendReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.addFirend:
            state = {
                empty: false,
                friends: action.payload
            }
            return state;
        case types.addOneFirend:
            const index = state.friends.findIndex(friend => friend._id === action.payload._id);
            state.friends[index] = action.payload;
            state.empty = false;
            return { ...state };
        case types.writing:
            index = state.friends.findIndex(friend => friend._id === action.payload.from);
            state.friends[index].writing = true;
            state.empty = false;
            return { ...state };
        case types.writingStop:
            index = state.friends.findIndex(friend => friend._id === action.payload.from);
            state.friends[index].writing = false;
            state.empty = false;
            return { ...state };
        default:
            return state;
    }
}
