import { types } from "../types"

export const _addFrind = (frinds)=>({
    type: types.addFirend, 
    payload:frinds 
});

export const _addOneFriend = (friend)=>({
    type: types.addOneFirend, 
    payload:friend 
});

export const _writing = (friend)=>({
    type: types.writing, 
    payload:friend 
});

export const _writingStop = (friend)=>({
    type: types.writingStop, 
    payload:friend 
});